<template>
  <div class="home_bg">
    <!--  导航栏-->
    <NavBar class="home_nav">
      <div slot="center">初声</div>
    </NavBar>
    <!--  麦克风动画效果-->
    <div @click="turnClick" style="background-color: rgba(255,255,255,0);border: none">
      <el-card class="home_center" shadow="never">
        <div><img src="@/assets/img/home/sing.png"></div>
        <div class="small"></div>
        <div class="center" :class="{change:this.voice === 3}"></div>
        <div class="big" :class="{change:this.voice >= 2}"></div>
        <div class="none" :class="{change:this.voice >= 1}"></div>
      </el-card>
    </div>
  </div>
</template>

<script>
import NavBar from "@/components/navbar/NavBar";
import recording from "../../assets/js/record.js";
import Recorder from 'js-audio-recorder'

const lamejs = require('lamejs')
const recorder = new Recorder({
  sampleBits: 8,                 // 采样位数，支持 8 或 16，默认是16
  sampleRate: 48000,              // 采样率，支持 11025、16000、22050、24000、44100、48000，根据浏览器默认值，我的chrome是48000
  numChannels: 1,                 // 声道，支持 1 或 2， 默认是1
  // compiling: false,(0.x版本中生效,1.x增加中)  // 是否边录边转换，默认是false
})
export default {
  name: "Home",
  components: {
    NavBar
  },
  data() {
    return {
      flag: true,
      video: {},
      transformList: [],
      small: 'small',
      center: 'center',
      big: 'big',

      recorder: null,
      interval: "",
      DB: 0,
      voice: 0,
      audioFileList: []
    }
  },
  created() {
    //  获取录音权限
    Recorder.getPermission().then(() => { //点击录音按钮获取权限，也可在created里进入页面就获取录音权限
    }, (error) => {
      console.log(`${error.name} : ${error.message}`);
    });
  },
  methods: {
    turnClick() {
      if (this.flag === true) {
        this.startRecorder()
        this.flag = false
      } else {
        this.stopRecorder()
        this.flag = true
      }
    },
    /**
     *  录音的具体操作功能
     * */
    // 开始录音
    startRecorder() {
      this.$message.info('开始翻译')
      //开始录音
      recorder.start().then(() => {
      }, (error) => {
        // 出错了
        console.log(`${error.name} : ${error.message}`);
      })

      var that = this;
      recording.get(rec => {
        if (rec) {
          this.recorder = rec
          this.interval = setInterval(() => {
            this.recorder.start();
            // 音频采集
            recording.recorder.onaudioprocess = function (e) {
              //开始处理音频
              var buffer = e.inputBuffer.getChannelData(0); //获得缓冲区的输入音频，转换为包含了PCM通道数据的32位浮点数组
              // 创建变量并迭代来获取最大的buffer中的音量值
              var maxVal = 0;
              for (var i = 0; i < buffer.length; i++) {
                if (maxVal < buffer[i]) {
                  maxVal = buffer[i];
                }
              }
              //显示音量值 在这获取到音量值
              that.DB = parseInt(maxVal * 10000);
            };
          }, 1000)
        }
      })

      this.timer = setTimeout(() => {
        this.stopRecorder()
      }, 15000)

    },
    // 结束录音
    stopRecorder() {
      this.$message.info('结束翻译，请等待结果')
      clearTimeout(this.timer)
      recorder.stop();
      this.recorder.stop()

      let formData = new FormData();
      let file = this.convertToMp3(recorder.getWAV())
      let mp3Name = encodeURIComponent('audio_recording_' + new Date().getTime() + '.mp3');
      file = new File([file], mp3Name, {
        type: "audio/mpeg",
      })
      formData.append('file', file);
      this.$http.post('cryTranslateWords/file', formData).then(res => {
        this.video = recorder
        if (res.data.status === 200) {
          this.transformList = res.data.data
          //发送录音信息
          this.$router.push({
            name: 'transform',
            params: {
              msgKey: this.video,
              transform: this.transformList
            }
          })
        } else {
          this.$message.warning('没有听清，请重新点击')
        }
      }).catch(err => {
        console.log(err);
      })
    },
    convertToMp3(wavDataView) {
      // 获取wav头信息
      const wav = lamejs.WavHeader.readHeader(wavDataView); // 此处其实可以不用去读wav头信息，毕竟有对应的config配置
      const {channels, sampleRate} = wav;
      const mp3enc = new lamejs.Mp3Encoder(channels, sampleRate, 128);
      // 获取左右通道数据
      const result = recorder.getChannelData()
      const buffer = [];

      const leftData = result.left && new Int16Array(result.left.buffer, 0, result.left.byteLength / 2);
      const rightData = result.right && new Int16Array(result.right.buffer, 0, result.right.byteLength / 2);
      const remaining = leftData.length + (rightData ? rightData.length : 0);

      const maxSamples = 1152;
      for (let i = 0; i < remaining; i += maxSamples) {
        const left = leftData.subarray(i, i + maxSamples);
        let right = null;
        let mp3buf = null;

        if (channels === 2) {
          right = rightData.subarray(i, i + maxSamples);
          mp3buf = mp3enc.encodeBuffer(left, right);
        } else {
          mp3buf = mp3enc.encodeBuffer(left);
        }

        if (mp3buf.length > 0) {
          buffer.push(mp3buf);
        }
      }

      const enc = mp3enc.flush();

      if (enc.length > 0) {
        buffer.push(enc);
      }

      return new Blob(buffer, {type: 'audio/mp3'});

    }
  },
  watch: {
    DB(newDb) {
      if (newDb >= 1 && newDb <= 100) {
        this.voice = 1;
      } else if (newDb >= 101 && newDb <= 200) {
        this.voice = 2;
      } else {
        this.voice = 3
      }
    }
  }
}
</script>

